 /**
  *loadMoreloading,,
  *refreshrefresh,
  *
  *uwsUpdate
  */
//tab
<template>
  <div class="wrap" ref="wrap">
    <!--  -->
    <div class="scroll-content" ref="container">
      <ul class="list-wrap">
        <li>
          <slot>
            <!--  -->
          </slot> 
        </li>
      </ul> 
        <!--  -->
      <div class="pullup-wrap" v-if="pullUpLoad">
        <div class="before-trigger" v-if="!isPullUpLoad">
          <span>{{pullUpTxt}}</span>
        </div>
        <div class="after-trigger" v-else>
          <up-loading/>
        </div>
      </div><!--upend-->
    </div>
    <!--  -->
    <div class="pulldown-wrap" ref="pulldown" :style="pullDownStyle" v-if="pullDownRefresh">
      <!-- start -->
      <div class="before-trigger" v-if="beforePullDown">
        <down-refresh :y="refreshY"/>
      </div><!--end-->
      <!-- endPull -->
      <div class="after-trigger" v-else>
        <div class="loading" v-if="pulling">
          <up-loading/>
        </div>
        <div class="refresh-info" v-else>
          <span>{{refreshTxt}}</span>
        </div>
      </div><!--loadend-->
    </div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  import DownRefresh from './refresh' 
  import UpLoading from './Loading'
  export default{
    name: 'Scroll',
    components: {
      DownRefresh,
      UpLoading
    },
    props: {
      probeType: {
        type: Number,
        default: 1
      },
      scrollY: {
        type: Boolean,
        default: true
      },
      /** */
      refreshDelay: {
        type: Number,
        default: 20
      },
      /** */
      click: {
        type: Boolean,
        default: true
      },
      /** */
      startY: {
        type: Number,
        default: 0
      },
      /**
       *  false
       *  true  Object 
       *  fade 
       */
      scrollbar: {
        type: Boolean,
        default: false
      },
      /** false
       *  true  Object 
       * *
       **/
      pullUpLoad: {
        type: null,
        default: () => {
          return {
            threshold: 100,
            loadInfo: {
              more: '...',
              noMore: ''
            }
          }
        }
      },
      /***
       *  false
       *  true  Object 
       * threshold stop
       * */
      pullDownRefresh: {
        type: null,
        default:  () => {
          return {
            threshold: 70,
            refreshInfo: {
              more: ''
            }
          }
        }
      },
      /****/
      beforeScroll: {
        type: Boolean,
        default: false
      },
      /** */
      listenScroll: {
        type: Boolean,
        default: false
      },
      list: {//
        type: Array,
        default: () => {
          return []
        }
      }
    },
    data() {
      return {
        beforePullDown: true,
        isPullingDown: false,
        isRebounding: false,
        isPullUpLoad: false,
        pulling: false,
        pullUpDirty: true,
        refreshY: 0,
        pullDownStyle: '',
        pullDowninitTop: -50
      }
    },
    computed: {
      pullUpTxt() {
        let moreTxt = this.pullUpLoad && this.pullUpLoad.loadInfo && this.pullUpLoad.loadInfo.more
        let noMoreTxt = this.pullUpLoad && this.pullUpLoad.loadInfo && this.pullUpLoad.loadInfo.noMore
        return this.pullUpDirty ? moreTxt : noMoreTxt
      },
      refreshTxt() {
        return this.pullDownRefresh && this.pullDownRefresh.refreshInfo.more
      }
    },
    mounted() {
      this.$nextTick(() => {
        new Promise((resolve, reject) => {
          let succ = this._getWrapHeight()
          if('succ' === succ){
            resolve()
          }else{
            reject()
          }
        }).then(() => {
          this.initScroll()
        })
      })
    },
    methods: {
      initScroll() {
         if(!this.$refs.wrap){//
           return
         }
         /**
          * 
          */
        //  if(this.$refs.container && (this.pullDownRefresh || this.pullUpLoad)){
        //    this.$refs.container.style.minHeight = this.wrapHeight + 1+ 'px'
        //  }
         /**
          * 
          */
         let options = {
           probeType: this.probeType,
           click: this.click,
           scrollY: this.scrollY,
           scrollbar: this.scrollbar,
           pullDownRefresh: this.pullDownRefresh,
           pullUpLoad: this.pullUpLoad,
           startY: this.startY
         }
         this.scroll = new BScroll(this.$refs.wrap, options)
         /**
         * 
         */
        if(this.beforeScroll){
          this.scroll.on('beforeScrollStart',() => {
            this.$emit('beforeScroll')
          })
        }
        /**
         * 
         * scroll
         * pos
         */
        if(this.listenScroll){
          this.scroll.on('scroll',(pos) => {
            this.$emit('scroll',pos)
          })
        }
        /**
         * 
         */
        if(this.pullDownRefresh){
          this._initPullDown()
        }
        /**
         * 
         */
        if(this.pullUpLoad){
          this._initPullUp()
        }
      },
      _getWrapHeight() {
        this.wrapHeight =document.documentElement.clientHeight - this.$refs.wrap.getBoundingClientRect().top;
        this.$refs.wrap.style.height = this.wrapHeight + 'px';
        if(this.$refs.container && (this.pullDownRefresh || this.pullUpLoad)){
           this.$refs.container.style.minHeight = this.wrapHeight + 1+ 'px'
         }
        return 'succ'
      },
      // 
      _initPullDown() {
        this.scroll.on('pullingDown', (pos) => {
          this.beforePullDown = false
          this.isPullingDown = true
          this.pulling = true
          this.$emit('refresh','refresh')
        })
        this.scroll.on('scroll', (pos)=>{
          if(this.beforePullDown){
            this.refreshY = Math.max(0, pos.y + this.pullDowninitTop)
            this.pullDownStyle = `top:${Math.min(pos.y + this.pullDowninitTop,10)}px`
          }else{
            this.refreshY = 0;
          }
          if(this.isRebounding){
            this.pullDownStyle = `top:${10 - (this.pullDownRefresh.stop-pos.y)}px`
          }
        })
      },
      //
      _initPullUp() {
        this.scroll.on('pullingUp', () => {
          this.isPullUpLoad = true
          this.$emit('loadMore','loading')
        })
      },
      _reboundPullDown() {
        let {stopTime = 600 } = this.pullDownRefresh
        return new Promise((resolve,reject) => {
          setTimeout(() => {
            this.isRebounding = true
            this.scroll.finishPullDown()
            this.isPullingDown = false,
            resolve()
          },stopTime)
        })
      },
      _afterPullDown() {
        setTimeout( ()=>{
          this.pullDownStyle = `top:${this.pullDowninitTop}px`
          this.beforePullDown = true
          this.isRebounding = false
          this.refresh()
        },this.scroll.options.bounceTime)
      },
      /**scroll */
      enable(){
        this.scroll && this.scroll.enable()
      },
      /**scroll */
      disable(){
        this.scroll && this.scroll.disable()
      },
      /**DOM */
      refresh() {
        this.scroll && this.scroll.refresh()
      },
      uwsUpdate(dirty) {
        if(this.pullDownRefresh && this.isPullingDown){
          this.pulling = false
          this._reboundPullDown().then(() => {
            this._afterPullDown()
          })
        }else if(this.pullUpLoad && this.isPullUpLoad){
          this.isPullUpLoad = false
          this.scroll.finishPullUp()
          this.pullUpDirty = dirty
          this.refresh()
        }else {
          this.refresh()
        }
      }
    },
    watch: {
      list(newVal,oldVal) {
        setTimeout(() => {
          this._getWrapHeight()
          this.uwsUpdate(true)
        },this.refreshDelay)
        
      }
    },
  }
</script>

<style lang="scss" scoped>
  @import '../../style/mixin';
  $border-color: rgba(163,163,163,1);
  .wrap{
    position: relative;
    overflow: hidden;
    margin-top: .04rem /* 3/75 */;
  }
  .scroll-content{
    width: 100%;
    box-sizing: border-box;
    // padding: 0 .346667rem /* 26/75 */;
    .list-wrap{
      position: relative;
      z-index: 100;
    }
  }
  .pulldown-wrap{
    position: absolute;
    width: 100%;
    left: 0;
    // top:-.666667rem /* 50/75 */;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all;
    .after-trigger{
      margin-top: 10px;
    }
    .refresh-info{
      color: rgba(50,135,193,.5);
      @include font-dpr(12px);
    }
  }
  .pullup-wrap{
    @include font-dpr(12px);
    color: rgba(50,135,193,.6);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: .213333rem /* 16/75 */ 0;
  }
</style>




